import imageCompression from "browser-image-compression";

export default {
    async compression(image: File) {
        const options = {
            maxSizeMB: 1,
            maxWidthOrHeight: 800
        };
        try {
            return await imageCompression(image, options);
        } catch (error) {
            console.error("compressionError", error);
            throw error;
        }
    }
}