import { 
    Container, 
    Box, 
    Grid,
    Typography,
    FormControl,
    Select,
    MenuItem,
    TextField,
    Button,
    CircularProgress,
    Alert,
    AlertTitle, 
} from "@mui/material";
import React from "react";
import { useState, ReactNode } from "react";
import axios, { AxiosResponse } from 'axios';
import compress from "./compress";

type Ingredient = {
    name: string;   //id
    quantity: string;
};

type Recipe = {
    category_id: number | string;
    title: string;
    description: string;
    servings: number | string;
    ingredients: Ingredient[];
    allergens: string[];    //id
    tags: string[];         //id
    instructions: string[];
};

type Request = {
    recipe: Recipe;
    image: File | null;
}

const initialState: Recipe = { 
    category_id: 1,
    title: "",
    description: "",
    servings: 1,
    ingredients: [{ name: "", quantity: "" }],
    allergens: [""],
    tags: [""],
    instructions: [""]
};

const baseUrl = "/api/post_recipe";

const Main = () => {
    const [request, setRequest] = useState<Request>({ recipe: initialState, image: null });
    const [onProgress, setOnProgress] = useState<boolean>(false)
    const [response, setResponse] = useState<AxiosResponse | null>();

    const sendRequest = async () => {
        setOnProgress(true);

        const formData = new FormData();
        formData.append("recipe", JSON.stringify(request.recipe));
        formData.append("image", request.image ?? "");

        await axios.post(baseUrl, formData, {
            headers: { 'Content-Type': 'multipart/form-data' }
        })
            .then((res) => {
                setResponse(res);
                console.log(res.data);
                setOnProgress(false);
            })
            .catch((error) => {
                console.log(error);
                setOnProgress(false);
            });
    };

    return (
        <Container>
            {onProgress
            ?
                <Grid container
                    direction='column'
                    alignItems='center'
                >
                    <Grid item>
                        <Grid container
                            direction='row'
                            alignItems='center'
                        >
                            <Grid item><CircularProgress /></Grid>
                        </Grid>
                    </Grid>
                </Grid>
            :
                <Box
                    sx={{
                        p: 2,
                        m: 2,
                    }}
                >
                    <Grid container
                        direction='column'
                        gap={2}
                        sx={{
                            pl: 2,
                        }}
                    >
                        {/* Title */}
                        <Grid item>
                            <GridRow>
                                <LeftItem>
                                    <Typo></Typo>
                                </LeftItem>
                                <RightItem>
                                    <TextField
                                        size='small'
                                        value={request.recipe.title ?? ""}
                                        InputProps={{ inputProps: {
                                            min: 1,
                                            max: 50,
                                            style: { textAlign: 'left' }
                                        }}}
                                        onChange={(event) => 
                                            setRequest({ ...request, recipe: { ...request.recipe, title: event.target.value } })
                                        }
                                    />
                                </RightItem>
                            </GridRow>
                        </Grid>
                        {/* Photo */}
                        <Grid item>
                            <GridRow>
                                <LeftItem>
                                    <Typo></Typo>
                                </LeftItem>
                                <Grid item>
                                    <UploadButton setImage={(image) => 
                                        setRequest({ ...request, image: image })
                                    } />
                                </Grid>
                                <Grid item>
                                    <Typography
                                        variant='body2'
                                        sx={{ overflow: 'hidden', textOverflow: 'ellipsis' }}
                                    >
                                        {request.image?.name}
                                    </Typography>
                                </Grid>
                            </GridRow>
                        </Grid>
                        {/* Category */}
                        <Grid item>
                            <GridRow>
                                <LeftItem>
                                    <Typo></Typo>
                                </LeftItem>
                                <RightItem>
                                    <FormControl size='small' sx={{ overflow: 'hidden', width: 150 }}>
                                        <Select
                                            value={request.recipe?.category_id ?? 1}
                                            onChange={(event) =>
                                                setRequest({ ...request, recipe: { ...request.recipe, category_id: event.target.value > 1 ? event.target.value : 1 } })
                                            }
                                        >
                                            <MenuItem value={1}></MenuItem>
                                            <MenuItem value={2}></MenuItem>
                                            <MenuItem value={3}></MenuItem>
                                            <MenuItem value={4}></MenuItem>
                                            <MenuItem value={5}></MenuItem>
                                            <MenuItem value={6}></MenuItem>
                                            <MenuItem value={7}></MenuItem>
                                        </Select>
                                    </FormControl>
                                </RightItem>
                            </GridRow>
                        </Grid>
                        {/* Description */}
                        <Grid item>
                            <GridRow>
                                <LeftItem>
                                    <Typo></Typo>
                                </LeftItem>
                                <RightItem>
                                    <TextField
                                        fullWidth
                                        multiline
                                        rows={2}
                                        size='small'
                                        value={request.recipe.description ?? ""}
                                        InputProps={{ inputProps: {
                                            min: 0,
                                            max: 255,
                                            style: { textAlign: 'left' }
                                        }}}
                                        onChange={(event) => 
                                            setRequest({ ...request, recipe: { ...request.recipe, description: event.target.value } })
                                        }
                                    />
                                </RightItem>
                            </GridRow>
                        </Grid>
                        {/* Servings */}
                        <Grid item>
                            <GridRow>
                                <LeftItem>
                                    <Typo></Typo>
                                </LeftItem>
                                <RightItem>
                                    <TextField
                                        size='small'
                                        type='number'
                                        style={{ width: 80 }}
                                        placeholder='priority'
                                        value={request.recipe.servings ?? 1}
                                        InputProps={{ inputProps: {
                                            min: 1,
                                            style: { textAlign: 'right' }
                                        }}}
                                        // TODO
                                        onChange={(event) => 
                                            setRequest({ ...request, recipe: { ...request.recipe, servings: parseInt(event.target.value) >= 1 ? parseInt(event.target.value) : 1 } })
                                        }
                                    /><Typo></Typo>
                                </RightItem>
                            </GridRow>
                        </Grid>
                        {/* Ingredients */}
                        <Grid item>
                            <GridRow>
                                <Grid item xs={5}>
                                    <Typo></Typo>
                                </Grid>
                                <Grid item xs={5}>
                                    <Typo></Typo>
                                </Grid>
                            </GridRow>
                            {request.recipe.ingredients.map((ingredient, index) => {
                                return (
                                    <Box
                                        key={index}
                                        sx={{
                                            p: 1,
                                            border: 1,
                                            borderColor: 'silver',
                                        }}
                                    >
                                        <GridRow>
                                            <Grid item xs={5}>
                                                <TextField
                                                    size='small'
                                                    value={ingredient.name ?? ""}
                                                    InputProps={{ inputProps: {
                                                        min: 1,
                                                        max: 50,
                                                        style: { textAlign: 'left' }
                                                    }}}
                                                    onChange={(event) =>
                                                        setRequest({ ...request, recipe: { ...request.recipe, 
                                                            ingredients: request.recipe.ingredients.map((ingr, ind) => 
                                                                ind === index ? { ...ingr, name: event.target.value } : ingr
                                                            )
                                                        }})
                                                    }
                                                />
                                            </Grid>
                                            <Grid item xs={5}>
                                                <TextField
                                                    size='small'
                                                    value={ingredient.quantity ?? ""}
                                                    InputProps={{ inputProps: {
                                                        min: 1,
                                                        max: 50,
                                                        style: { textAlign: 'left' }
                                                    }}}
                                                    onChange={(event) => 
                                                        setRequest({ ...request, recipe: { ...request.recipe, 
                                                            ingredients: request.recipe.ingredients.map((ingr, ind) => 
                                                                ind === index ? { ...ingr, quantity: event.target.value } : ingr
                                                            )
                                                    }})
                                                    }
                                                />
                                            </Grid>
                                        </GridRow>
                                    </Box>
                                );
                            })}
                            <Grid container
                                direction='row'
                                alignItems='center'
                                justifyContent='flex-end'
                                gap={2}
                            >
                                <Grid item>
                                    <Button
                                        variant='outlined'
                                        sx={{ textTransform: 'none' }}
                                        onClick={() => 
                                            setRequest({ ...request, recipe: { ...request.recipe,  
                                                ingredients: request.recipe.ingredients.concat([{ name: "", quantity: "" }]) 
                                            }})
                                        }
                                    >
                                        Add a Row
                                    </Button>
                                </Grid>
                                {/* <Grid item>
                                    <Button
                                        variant='outlined'
                                        sx={{ textTransform: 'none' }}
                                        onClick={upDateRules}
                                    >
                                        Save Rules
                                    </Button>
                                </Grid> */}
                            </Grid>
                        </Grid>
                        {/* Tags */}
                        <Grid item>
                            <GridRow>
                                <LeftItem>
                                    <Typo></Typo>
                                </LeftItem>
                                <Grid item>
                                    <Grid container
                                        direction='column'
                                        alignItems='center'
                                        gap={2}                            
                                    >
                                        {request.recipe.tags.map((tag, index) => {
                                            return (
                                                <div key={index}><GridRow>
                                                    <Grid item>
                                                        <TextField
                                                            size='small'
                                                            value={tag ?? ""}
                                                            InputProps={{ inputProps: {
                                                                min: 1,
                                                                max: 20,
                                                                style: { textAlign: 'left' }
                                                            }}}
                                                            onChange={(event) => 
                                                                setRequest({ ...request, recipe: { ...request.recipe, 
                                                                    tags: request.recipe.tags.map((t, i) => 
                                                                        i === index ? event.target.value : t
                                                                    )
                                                                }})
                                                            }
                                                        />
                                                    </Grid>
                                                </GridRow></div>
                                            );
                                        })}
                                    </Grid>
                                </Grid>
                            </GridRow>
                            <Grid container
                                direction='row'
                                alignItems='center'
                                justifyContent='flex-end'
                                gap={2}
                            >
                                <Grid item>
                                    <Button
                                        variant='outlined'
                                        sx={{ textTransform: 'none' }}
                                        onClick={() => 
                                            setRequest({ ...request, recipe: { ...request.recipe,
                                                tags: request.recipe.tags.concat([""]) 
                                            }})
                                        }
                                    >
                                        Add a Row
                                    </Button>
                                </Grid>
                                {/* <Grid item>
                                    <Button
                                        variant='outlined'
                                        sx={{ textTransform: 'none' }}
                                        onClick={upDateRules}
                                    >
                                        Save Rules
                                    </Button>
                                </Grid> */}
                            </Grid>
                        </Grid>
                        {/* Instructions */}
                        <Grid item>
                            <GridRow>
                                <Grid item xs={5}>
                                    <Typo></Typo>
                                </Grid>
                                <Grid item xs={5}>
                                    <Typo></Typo>
                                </Grid>
                            </GridRow>
                            {request.recipe.instructions.map((instruction, index) => {
                                return (
                                    <Box
                                        key={index}
                                        sx={{
                                            p: 1,
                                            border: 1,
                                            borderColor: 'silver',
                                        }}
                                    >
                                        <GridRow>
                                            <Grid item xs={5}>
                                                <Typo>{index + 1}</Typo>
                                            </Grid>
                                            <Grid item xs={5}>
                                                <TextField
                                                    size='small'
                                                    value={instruction ?? ""}
                                                    InputProps={{ inputProps: {
                                                        min: 1,
                                                        max: 255,
                                                        style: { textAlign: 'left' }
                                                    }}}
                                                    onChange={(event) => 
                                                        setRequest({ ...request, recipe: { ...request.recipe,
                                                            instructions: request.recipe.instructions.map((inst, i) => 
                                                                i === index ? event.target.value : inst
                                                            )
                                                        }})
                                                    }
                                                />
                                            </Grid>
                                        </GridRow>
                                    </Box>
                                );
                            })}
                            <Grid container
                                direction='row'
                                alignItems='center'
                                justifyContent='flex-end'
                                gap={2}
                            >
                                <Grid item>
                                    <Button
                                        variant='outlined'
                                        sx={{ textTransform: 'none' }}
                                        onClick={() => 
                                            setRequest({ ...request, recipe: { ...request.recipe,
                                                instructions: request.recipe.instructions.concat([""]) 
                                            }})
                                        }
                                    >
                                        Add a Row
                                    </Button>
                                </Grid>
                                {/* <Grid item>
                                    <Button
                                        variant='outlined'
                                        sx={{ textTransform: 'none' }}
                                        onClick={upDateRules}
                                    >
                                        Save Rules
                                    </Button>
                                </Grid> */}
                            </Grid>
                        </Grid>
                        <Grid>
                            <Grid container
                                direction='row'
                                alignItems='center'
                                justifyContent='flex-end'
                                gap={2}
                            >
                                {/* <Grid item>
                                    <Button
                                        variant='outlined'
                                        sx={{ textTransform: 'none' }}
                                        onClick={() => 
                                            setRecipe({ ...recipe, 
                                                instructions: recipe.instructions.concat([""]) 
                                            })}
                                    >
                                        Add a Row
                                    </Button>
                                </Grid> */}
                                <Grid item>
                                    <Button
                                        variant='outlined'
                                        sx={{ textTransform: 'none' }}
                                        onClick={sendRequest}
                                    >
                                        SEND
                                    </Button>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    {response
                    ?
                        <Grid container
                            direction='column'
                            alignItems='center'
                            gap={1}
                        >
                            <Grid item>
                                <Alert
                                    severity={response.status == 200 ? 'success' : 'error'}
                                    onClose={() => setResponse(null)}
                                >
                                    {response.data['status']}
                                </Alert>
                            </Grid>
                        </Grid>
                    :
                        <div></div>
                    }
                </Box>
            }
        </Container>
    );
};

const GridRow = ({children}: {children?: ReactNode | undefined}) => {
    return (
      <Grid container
        direction='row'
        alignItems='center'
        gap={2}
      >
        {children}
      </Grid>
    );
};

const LeftItem = ({children}: {children?: ReactNode | undefined}) => {
    return (
      <Grid item xs={3}>
        <Box display='flex' justifyContent='flex-start'>
          {children}
        </Box>
      </Grid>
    );
};

const RightItem = ({children}: {children?: ReactNode | undefined}) => {
    return (
      <Grid item xs={8}>
        <Box display='flex' justifyContent='flex-start'>
          {children}
        </Box>
      </Grid>
    );
};

const Typo = ({children}: {children?: ReactNode | undefined}) => {
    return (
      <Typography
        variant='h6'
        sx={{ overflow: 'hidden', textOverflow: 'ellipsis' }}
      >
        {children}
      </Typography>
    );
};

const UploadButton = ({setImage}: {setImage: (image: File) => void}) => {
    return (
        <label htmlFor="upload-photo">
            <input
                style={{ display: 'none' }}
                id="upload-photo"
                name="upload-photo"
                type="file"
                accept="image/png, image/jpeg"
                onChange={async (event) => {
                    const img = await compress.compression(event.target.files![0]);
                    setImage(img);
                }}
            />
                <Button color="secondary" variant="contained" component="span">
                    Upload Photo
                </Button>
        </label>
    );
}

export default Main;